<template>
  <div class='box'>
      <loading v-if='isShow'></loading>
      <div class='wrap'>
        <div class='container'>
            <div class='swpier'>
              <img src='../assets/image/banner.png' alt=''>
            </div>
            <div class='describe'>
              <div class='title'>
                {{name}}
              </div>
              <div class='info'>
                <grid :dataList = dataList :dataKey = dataKey v-if='dataList'></grid>
              </div>
            </div>
        </div>
      </div>
  </div>
</template>

<script>
import axios from 'axios'
import grid from '@/components/grid'
import loading from '@/components/loading'
export default {
  name: 'order',
  data () {
    return {
      isShow: true,
      response: {},
      dataList: [
        {key: '产品名称', val: '有机西红柿'},
        {key: '配料', val: '西红柿'},
        {key: '净含量', val: '1000g'},
        {key: '保质期', val: '6个月'},
        {key: '储存条件', val: '请置于阴凉,干燥，通风处'},
        {key: '生产许可证', val: 'SC13254126'},
        {key: '生产标准', val: 'GB/T 10032'},
        {key: '生产企业', val: '河南平顶山农产品有限公司'},
        {key: '生产地址', val: '河南省平顶山'}
      ],
      name: '',
      dataKey: {
        pro_name: '进货名称',
        sup_name: '农产名称',
        im_patch: '进货批次',
        recipient: '负责人',
        im_time: '进货时间',
        im_num: '进货量',
        result: '验收状态'
      }
    }
  },
  components: {
    loading,
    grid
  },
  mounted () {
    this.getData()
  },
  methods: {
    getData () {
      console.log('data')
      axios.get('/api/sweep?batch=20181022135308').then(this.getDataSucc)
        .catch(err => {
          this.isShow = false
          console.log(err)
        })
    },
    getDataSucc (res) {
      // console.log(res.data.im_porduct)
      if (res && res.data) {
        this.isShow = false
        this.name = res.data.im_porduct.pro_name
        this.response = res.data.im_porduct
      } else {
        alert('请求失败，稍后重试')
      }
    }
  }
}
</script>

<style scoped>
  .box{
    width: 100%;
    height: 100%;
    background-color: #f5f5f5;
  }
  .wrap{
    height: 100%;
    max-width: 10rem;
    padding-bottom:1rem;
    box-sizing:border-box;
    -moz-box-sizing:border-box; /* Firefox */
    -webkit-box-sizing:border-box; /* Safari */
    margin: 0 auto;
    background-color:#f5f5f5;
    overflow: hidden;
    overflow-y: auto;
  }
  .container{
    margin:0.3rem;
    margin-top:1.5rem;
    padding: 0.2rem 0.2rem 1rem;
    padding-top:1.2rem;
    border-radius:0.1rem;
    background:#fff;
    position: relative;

  }
  .swpier{
    width: 2rem;
    height: 2rem;
    margin-left:-1rem;
    position: absolute;
    top:-1rem;
    left:50%;
  }
  .swpier img{
    width: 100%;
    height: 100%;
    display: block;
    border-radius:50%;
  }
  .describe{
    width: 100%;
    margin-top:0.1rem;
    overflow: hidden;
  }
  .title{
    margin-bottom:0.3rem;
    width: 100%;
    text-align: center;
    font-size: 0.34rem;
    font-weight: 600;
    color: #333;

  }
  .info{
    width: 95%;
    margin: 0 auto;
  }

</style>



