<template>
  <div class="box">
      <loading v-if='isShow'></loading>
      <div class='wrap'>
        <div class='container'>
            <div class='swpier'>
              <img src='../assets/image/banner.png' alt=''>
            </div>
            <div class='describe'>
              <div class='title'>
                {{name}}
              </div>
              <div class='info'>
                <p>河南省平顶山市农产品有限公司河南省平顶山市农产品有限公司河南省平顶山市农产品有限公司河南省平顶山市农产品有限公司河南省平顶山市农产品有限公司河南省平顶山市农产品有限公司</p>
              </div>
            </div>
            <grid :dataList = dataList :dataKey = dataKey></grid>
        </div>
      </div>
  </div>
</template>

<script>
import axios from 'axios'
import loading from '@/components/loading'
import grid from '@/components/grid'
export default {
  name: 'enterprise',
  data () {
    return {
      isShow: true,
      name: '',
      response: {},
      dataList: [
        {key: '生产基地', val: '平顶山1号基地'},
        {key: '所属机构', val: '河南省平顶山市农产品有限公司'},
        {key: '占地面积', val: '200亩'},
        {key: '负责人', val: '李胜利'},
        {key: '负责期限', val: '2018-8-1 至 2019-7-31'}
      ],
      dataKey: {
        p_form: '组织形式',
        p_industry: '所属行业',
        p_certificate: '证件类型',
        p_register: '工商注册号',
        p_address: '详细地址',
        p_time: '营业期限'
      }

    }
  },
  components: {
    loading,
    grid
  },
  mounted () {
    this.getData()
  },
  methods: {
    getData () {
      console.log('data')
      axios.get('/api/sweep?batch=20181022135308').then(this.getDataSucc)
        .catch(err => {
          this.isShow = false
          console.log(err)
        })
    },
    getDataSucc (res) {
      // console.log(res.data.im_porduct)
      if (res && res.data) {
        this.isShow = false
        this.name = res.data.providers.p_name
        this.response = res.data.providers
      } else {
        alert('请求失败，稍后重试')
      }
    }
  }
}
</script>

<style scoped>
  .box{
    width: 100%;
    height: 100%;
    background-color: #f5f5f5;
  }
  .wrap{
    height: 100%;
    max-width: 10rem;
    padding-bottom:1rem;
    box-sizing:border-box;
    -moz-box-sizing:border-box; /* Firefox */
    -webkit-box-sizing:border-box; /* Safari */
    margin: 0 auto;
    background-color:#f5f5f5;
    overflow: hidden;
  }
  .container{
    background-color:#fff;
    margin:0.2rem;
    padding: 0.3rem 0.3rem 1rem;
    border-radius:0.1rem;
  }
  .swpier{
    width: 100%;
  }
  .swpier img{
    width: 100%;
    height: 100%;
    display: block;
    border-radius:0.2rem;
  }
  .describe{
    width: 100%;
    margin-top:0.2rem;
    overflow: hidden;
  }
  .title{
    font-weight: 600;
    font-size: 0.34rem;
    color: #333;
    margin-bottom:0.2rem;
  }
  .info{
    font-size: .26rem;
    line-height: .36rem;
    color: #666;
  }
  .info p{
    text-indent:2em;
  }

</style>



