<template>
  <div class="box">
      <div class='wrap'>
        <div class='container'>
            <div class='swpier'>
              <img src='../assets/image/banner.png' alt=''>
            </div>
            <div class='describe'>
              <div class='title'>
                有机西红柿 1000g
              </div>
              <div class='info'>
                <img src="../assets/image/check.jpg" alt="" >
                <img src="../assets/image/check2.jpg" alt="" >
                <img src="../assets/image/check3.jpg" alt="" >
              </div>
            </div>
        </div>
      </div>
  </div>
</template>

<script>
import loading from '@/components/loading'
import grid from '@/components/grid'
export default {
  name: 'enterprise',
  data () {
    return {
      isShow: false,
      dataList1: [
        {key: '公司地址', val: '平顶山市松花区松花路135号'},
        {key: '企业编码', val: 'PDS54862295'},
        {key: '负责人', val: '黄毅'},
        {key: '联系方式', val: '15966866666'}
      ]

    }
  },
  components: {
    loading,
    grid
  },
  mounted () {
    this.getData()
  },
  methods: {
    getData () {
      setTimeout(() => {
        this.isShow = false
      }, 1000)
    }
  }
}
</script>

<style scoped>
  .box{
    width: 100%;
    height: 100%;
    background-color: #f5f5f5;
  }
  .wrap{
    height: 100%;
    max-width: 10rem;
    padding-bottom:1rem;
    box-sizing:border-box;
    -moz-box-sizing:border-box; /* Firefox */
    -webkit-box-sizing:border-box; /* Safari */
    margin: 0 auto;
    background-color:#f5f5f5;
    overflow: hidden;
    overflow-y: auto;
  }
  .container{
    margin:0.3rem;
    margin-top:1.5rem;
    padding: 0.2rem 0.2rem 1rem;
    padding-top:1.2rem;
    border-radius:0.1rem;
    background:#fff;
    position: relative;

  }
  .swpier{
    width: 2rem;
    height: 2rem;
    margin-left:-1rem;
    position: absolute;
    top:-1rem;
    left:50%;
  }
  .swpier img{
    width: 100%;
    height: 100%;
    display: block;
    border-radius:50%;
  }
  .describe{
    width: 100%;
    margin-top:0.1rem;
    overflow: hidden;
  }
  .title{
    margin-bottom:0.3rem;
    width: 100%;
    text-align: center;
    font-size: 0.34rem;
    font-weight: 600;
    color: #333;

  }
  .info img{
    display: block;
    width: 100%;
    height: 100%;
    margin-bottom: 0.2rem;
  }

</style>



